import { Link } from 'react-router-dom'

import SideBarIcon from '../../components/SideBarIcon/SideBarIcon'
import {
    MenuIcon,
    PersentIcon,
    PizzaIcon,
    MassegeIcon,
    NotionIcon,
    SettingIcon,
    LeaveIcon
} from '../../assets/icons/Icons'

import SidebarLogo from '../../assets/images/icons/Logo.svg'
import './Sidebar.scss'

const Sidebar = ({activePage, setActivePage}) => {
    return(
        <div className="sidebar">
            <a href="javascript:void(0)" className="sidebar-logo">
                <img src={SidebarLogo} alt=""/>
            </a>
            <Link to="/">
                <SideBarIcon
                icon={<MenuIcon/>}
                onClick={() => setActivePage('Home')}
                active={activePage == 'Home'}
                /> 
            </Link>
            
            <Link to="/Page2">
                <SideBarIcon icon={<PersentIcon/>}
                active={activePage == 'Page2'}
                onClick={() => setActivePage('Page2')}
                />
            </Link>

            <Link to="/Page3">
                <SideBarIcon icon={<PizzaIcon/>}
                active={activePage == 'Page3'}
                onClick={() => setActivePage('Page3')}
                />
            </Link>
            <Link to="/Page4">
                <SideBarIcon icon={<MassegeIcon/>}
                active={activePage == 'Page4'}
                onClick={() => setActivePage('Page4')}/>
            </Link>
            <Link to="/Page5">
                <SideBarIcon icon={<NotionIcon/>}
                active={activePage == 'Page5'}
                onClick={() => setActivePage('Page5')}/>
            </Link>
            <Link to="/Page6">
                <SideBarIcon icon={<SettingIcon/>}
                active={activePage == 'Page6'}
                onClick={() => setActivePage('Page6')}/>
            </Link>
            <Link to="/Page7">
                <SideBarIcon icon={<LeaveIcon/>}
                active={activePage == 'Page7'}
                onClick={() => setActivePage('Page7')}/>
            </Link>
        </div>
    )
}

export default Sidebar;