import './MostFoods.scss'
import Chuchvara from '../../assets/images/chuchvara.png'

const MostFoods = () => {
    return (
        <div className="most-foods">
            <img src={ Chuchvara } alt=""/>
            <div className="food-titles">
                <h3 className="title">Spicy seasoned seafood noodles</h3>
                <p className="subtitle">200 dishes ordered</p>
            </div>
        </div>
    )
}

export default MostFoods;