import OrderList from '../../components/OrderList/OrderList'

import './Order.scss'

const Order = ({btnState,hidden, setBtnState}) => {
    return (
        <div className={`order ${btnState ? 'hidden' : ''}`}>
            <div className="order-header">
                <div className="header-top">
                    <h2 className="header-top__title">
                        Orders #34562
                    </h2>
                </div>
                <div className="header-center">
                    <button>Dine In</button>
                    <button>To Go</button>
                    <button>Delivery</button>
                </div>
                <div className="header-bottom">
                    <div className="bottom-left">
                        <h2 className="left__title">
                            Item
                        </h2>
                    </div>
                    <div className="bottom-right">
                        <h2 className="right__title">Qty</h2>
                        <h2 className="right__title2">Price</h2>
                    </div>
                </div>
            </div>

            <div className="orders-section">
                    <OrderList/>
                    <OrderList/>
                    <OrderList/>
                    <OrderList/>
                    <OrderList/>
                    <OrderList/>
            </div>

            <div className="payment-desk">
                <div className="discount">
                    <h2>Discount</h2>
                    <h3>$0</h3>
                </div>
                <div className="total">
                    <h2>Sub total</h2>
                    <h3>$ 21,03</h3>
                </div>

                <button onClick={() => setBtnState(!hidden)}>Continue to Payment</button>
            </div>
        </div>
    )
};

export default Order;