import Chuchvara from '../../assets/images/chuchvara.png'
import Delete from '../../assets/images/icons/delete.svg'

import './OrderList.scss'


const OrderList = () => {
    return (
        <div className="order-list">
            <div className="list">
                <div className="list-left">
                    <div className="left-top">
                        <img src={Chuchvara} alt=""/>
                        <div className="left-titles">
                            <h3 className="food__name">Spicy seasoned sea...</h3>
                            <h3 className="food__price">$ 2.29</h3>
                        </div>
                        <h1 className="food__number">2</h1>
                    </div>
                    <div className="left-bottom">
                        <input type="search" placeholder="Please, just a little bit spicy only."/>
                    </div>
                </div>

                <div className="list-right">
                    <h2 className="total__price">$ 4,58</h2>
                    <img src={Delete} alt=""/>
                </div>
            </div>
        </div>
    )
}

export default OrderList;