import './Calculation.scss'
import { DollarIcon } from '../../assets/icons/Icons'
import { UpArrow } from '../../assets/icons/Icons'

const Calculation = () => {
    return (
        <div className="block">
            <div className="block-top">
                <DollarIcon/>
                <h4 className="percent">+32.40%</h4>
                <UpArrow/>
            </div>
            <h1 className="block__title">
            $10,243.00
            </h1>
            <h4 className="block__subtitle">
            Total Revenue
            </h4>
        </div>
    )
}

export default Calculation;