import './Modal.scss'
import Checkbox from '../../components/Checkbox/Checkbox'

const Modal = ({btnState, setBtnState}) => {
    return (
        <div className="modal">
            <div className="modal-header">
                <h1 className="header__title">Payment</h1>
                <h4 className="header__subtitle">3 payment method available</h4>
            </div>

            <div className="form-section">
                <h1 className="form__title">
                    Payment Method
                </h1>
                <div className="buttons">
                    <button>
                        <Checkbox/>
                        <p>Credit card</p>
                    </button>
                    <button>
                        <Checkbox/>
                        <p>Credit card</p>
                    </button>
                    <button>
                        <Checkbox/>
                        <p>Credit card</p>
                    </button>
                </div>

                <h1 className="cardholder__name">Cardholder Name</h1>
                <input className="cardholder-name__input" placeholder="Levi Ackerman" type="text"/>

                <h1 className="cardholder__name">Card Numbere</h1>
                <input className="cardholder-name__input" placeholder="2564 1421 0897 1244" type="number"/>

                <div className="bottom-form">
                    <div className="left">
                        <h1 className="left__title">Expiration Date</h1>
                        <input className="left__input" placeholder="2564 1421 0897 1244" type="date"/>
                    </div>

                    <div className="right">
                        <h1 className="right__title">CVV</h1>
                        <input className="right__input" placeholder="***" type="password"/>
                    </div>
                </div>

                <div className="form-bottom">
                    <div className="left">
                        <h1 className="left__title">Order Type</h1>
                        <select className="left__input" name="" id="">
                            <option value="">Dine In</option>
                            <option value="">Dine In</option>
                            <option value="">Dine In</option>
                        </select>
                    </div>

                    <div className="right">
                        <h1 className="right__title">Table no.</h1>
                        <input className="right__input" placeholder="140" type="number"/>
                    </div>

                </div>

                <div className="bottom-buttons">
                        <button onClick={() => setBtnState(!btnState)} >Cancel</button>
                        <button>Confirm Payment</button>
                </div>
            </div>
            
        </div>
    )
}

export default Modal;