import './Contacts.scss';

const Contact = ({img, name, text, payment, status}) => {
    return (
        <div className="contact">
            <div className="img-wrapper">
                <img src={img} alt="" className="img-wrapper__img"/>
                <h4 className="img-wrapper__name">{name}</h4>  
            </div>

            <div className="text-wrapper">
                <h4 className="text-wrapper__text">
                    {text}
                </h4>
            </div>

            <h4 className="payment">
                {payment}
            </h4>

            <h4 className="status">
                {status}
            </h4>

        </div>
    )
}

export default Contact;