import MenuList from '../../components/MenuList/MenuList'

import './Main.scss'

const Main = () => {
    return (
        <div className="main">
                <div className="header">
                        <div className="header-top">
                            <div className="header-left">
                                <h2 className="header__title">Jaegar Resto</h2>
                                <h5 className="header__subtitle">Tuesday, 2 Feb 2021</h5>
                            </div>
                            <div className="header-right">
                                <input type="search" placeholder="Search for food, coffe, etc.."/>
                            </div>
                        </div>
                        <div className="header-bottom">
                            <div className="header-links">
                                <a className="links" href="">Hot Dishes</a>
                                <a className="links" href="">Cold Dishes</a>
                                <a className="links" href="">Soup</a>
                                <a className="links" href="">Grill</a>
                                <a className="links" href="">Appetizer</a>
                                <a className="links" href="">Dessert</a>
                            </div>
                            <div className="header-line">
                                <div className="line">
                                    <span></span>
                                </div>
                            </div>
                        </div>
                </div>

                <div className="tablet">
                    <div className="tablet-top">
                        <h2 className="tablet__title">Choose Dishes</h2>
                        <select name="" id="">
                            <option value="">Dine in</option>
                            <option value="">Dine in</option>
                            <option value="">Dine in</option>
                            <option value="">Dine in</option>
                        </select>
                    </div>
                </div>

                <div className="menu-list-card">
                    <MenuList/>  
                </div>
        </div>
    )
}

export default Main;